@font-face {
  font-family: "Berlin Sans FB";
  font-style: normal;
  font-weight: normal;
  src: local("Berlin Sans FB"), url("../fonts/BRLNSR.woff") format("woff");
}

@font-face {
  font-family: "Berlin Sans FB Bold";
  font-style: normal;
  font-weight: normal;
  src: local("Berlin Sans FB Bold"), url("../fonts/BRLNSB.woff") format("woff");
}

@keyframes jump {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  20% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
    transform: translateY(-30px);
  }
  50% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  60% {
    -webkit-transform: translateY(-15px);
    transform: translateY(-15px);
  }
  80% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  font-size: 62.5%;
}

body {
  background-color: #fcfcfc;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #777;
}

body {
  margin: 0;
}

.padding-slider {
  padding: 10px;
}

@media (max-width: 890px) {
  html {
    font-size: 58%;
  }
}
@media (max-width: 600px) {
  html {
    font-size: 50%;
  }
}

@media (max-width: 400px) {
  html {
    font-size: 47%;
  }
}

.modal {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.modal-img {
  width: 70%;
  height: 95%;
  max-height: 95%;
}

.overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(207, 207, 207, 0.8);
  z-index: 9000;
  backdrop-filter: blur(20px);
}

.overlay-two {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 9000;
  backdrop-filter: blur(20px);
}

.span-loading {
  display: block;
  font-family: "Saira Extra Condensed", sans-serif;
  font-size: 2.2rem;
  margin-top: 20px;
}

.modal-container {
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.9);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  color: #333;
  font-weight: 700;
  font-size: 16px;
  background-color: #f6f6f6;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.modal-header span {
  line-height: 25px;
}

.modal-content {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.modal-content-img {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 10px;
  border-top: 1px solid #ddd;
}

.content-modal-inputs {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

.btn-close-modal {
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 15px;
  background-color: #f44336;
  border-radius: 100%;
  width: 23px;
  height: 23px;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-close-modal:hover {
  opacity: 0.9;
}

.btn-close-modal:active {
  transform: scale(0.97);
}

.not-found {
  width: 100%;
  height: 500px;
  padding: 70px;
  font-size: 25px;
  color: #1a1a1a;
  font-weight: 600;
}

.link-product {
  display: block;
  width: 100%;
  text-align: center;
  color: #1a1a1a;
  text-decoration: none;
  font-size: 2rem;
  padding-bottom: 5px;
  font-family: "Saira Extra Condensed", sans-serif;
  font-weight: 500;
  margin-top: -8px;
}

.link-product:hover {
  text-decoration: underline;
}

.product-check {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 1.7rem;
  font-weight: 700;
  color: #555;
}

.img-index-events {
  position: relative;
  margin-bottom: -20rem;
  width: 150%;
}

.content-academia {
  background: linear-gradient(
    180deg,
    rgba(252, 252, 252, 1) 0%,
    rgba(221, 221, 221, 1) 100%
  );
  margin-top: 90px;
}

.btn-link-products {
  font-family: "Saira Extra Condensed", sans-serif;
  font-size: 2rem;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.btn-link-products:hover {
  text-decoration: underline;
}

.depoiments {
  background-color: #1a1a1a;
  padding: 20px 30px;
  margin-top: 60px;
}

.img-text {
  width: 60%;
}

.container-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  flex-direction: column;
}

.icone-loading {
  width: 200px;
  animation: jump 1.5s infinite;
}

.logo-loading {
  margin-top: 30px;
  height: 120px;
}

@media (max-width: 885px) {
  .content-academia {
    margin-top: 30px;
  }
}
@media (max-width: 820px) {
  .product-check {
    font-size: 1.5rem;
  }
}
@media (max-width: 760px) {
  .product-check {
    font-size: 1.3rem;
  }
  .img-index-events {
    width: 220%;
  }
}
@media (max-width: 675px) {
  .img-index-events {
    width: 80%;
    margin-bottom: 0;
  }
  .img-text {
    width: 80%;
  }
}
@media (max-width: 500px) {
  .img-text {
    width: 90%;
    margin-bottom: 10px;
  }
  .img-index-events {
    width: 90%;
  }
}
